import React, { useState, memo } from 'react';
import './Projects.css';

const Projects = memo(function Projects() {
  const projects = [
    {
      id: 1,
      title: 'Idea Funding',
      description: 'Idea FundingÏùÄ Ï∞ΩÏùòÏ†ÅÏù∏ ÏïÑÏù¥ÎîîÏñ¥Î•º Í∞ÄÏßÑ Ï∞ΩÏûëÏûêÏôÄ ÌõÑÏõêÏûêÎ•º Ïó∞Í≤∞ÌïòÎäî ÌÅ¨ÎùºÏö∞ÎìúÌéÄÎî© ÌîåÎû´ÌèºÏûÖÎãàÎã§. ÏÇ¨Ïö©ÏûêÎì§ÏùÄ Îã§ÏñëÌïú ÌîÑÎ°úÏ†ùÌä∏Î•º ÌÉêÏÉâÌïòÍ≥†, Í¥ÄÏã¨ ÏûàÎäî ÌîÑÎ°úÏ†ùÌä∏Ïóê ÌõÑÏõêÌï† Ïàò ÏûàÏúºÎ©∞, Ïª§ÎÆ§ÎãàÌã∞Î•º ÌÜµÌï¥ ÏïÑÏù¥ÎîîÏñ¥Î•º Í≥µÏú†ÌïòÍ≥† ÏÜåÌÜµÌï† Ïàò ÏûàÏäµÎãàÎã§.',
      link: 'https://exile175r.github.io/idea-funding',
      image: `${process.env.PUBLIC_URL}/image/ideaFunding.png`
    },
    // {
    //   id: 2,
    //   title: 'Project 2',
    //   description: 'ÌîÑÎ°úÏ†ùÌä∏ ÏÑ§Î™ÖÏùÑ Ïó¨Í∏∞Ïóê ÏûëÏÑ±ÌïòÏÑ∏Ïöî.',
    //   link: 'https://example.com',
    //   image: null
    // },
    // {
    //   id: 3,
    //   title: 'Project 3',
    //   description: 'ÌîÑÎ°úÏ†ùÌä∏ ÏÑ§Î™ÖÏùÑ Ïó¨Í∏∞Ïóê ÏûëÏÑ±ÌïòÏÑ∏Ïöî.',
    //   link: 'https://example.com',
    //   image: null
    // }
  ];

  const [imageErrors, setImageErrors] = useState({});
  const [imageLoading, setImageLoading] = useState(() => {
    // Ï¥àÍ∏∞ Î°úÎî© ÏÉÅÌÉú: Ïù¥ÎØ∏ÏßÄÍ∞Ä ÏûàÎäî ÌîÑÎ°úÏ†ùÌä∏Îäî Î°úÎî© Ï§ëÏúºÎ°ú ÏÑ§Ï†ï
    const initialLoading = {};
    projects.forEach(project => {
      if (project.image) {
        initialLoading[project.id] = true;
      }
    });
    return initialLoading;
  });

  const handleImageError = (projectId) => {
    setImageErrors(prev => ({ ...prev, [projectId]: true }));
    setImageLoading(prev => ({ ...prev, [projectId]: false }));
  };

  const handleImageLoad = (projectId) => {
    setImageLoading(prev => ({ ...prev, [projectId]: false }));
  };

  const shouldShowImage = (project) => {
    // Ïù¥ÎØ∏ÏßÄ Í≤ΩÎ°úÍ∞Ä ÏûàÍ≥†, ÏóêÎü¨Í∞Ä ÏóÜÏùÑ Îïå
    return project.image && !imageErrors[project.id];
  };

  return (
    <section className="projects" id="projects">
      <div className="projects-container">
        <h2 className="projects-title">Projects</h2>
        <p className="projects-subtitle">Ï†úÏûëÌïú ÌîÑÎ°úÏ†ùÌä∏Îì§ÏùÑ ÏÜåÍ∞úÌï©ÎãàÎã§</p>
        <div className="projects-grid">
          {projects.map((project) => (
            <div key={project.id} className="project-card">
              <div className="project-image">
                {shouldShowImage(project) ? (
                  <>
                    {imageLoading[project.id] && (
                      <div className="project-placeholder">
                        <div className="placeholder-text">Î°úÎî© Ï§ë...</div>
                      </div>
                    )}
                    <img
                      src={project.image}
                      alt={project.title}
                      onError={() => handleImageError(project.id)}
                      onLoad={() => handleImageLoad(project.id)}
                      style={{ display: imageLoading[project.id] ? 'none' : 'block' }}
                    />
                  </>
                ) : (
                  <div className="project-placeholder">
                    <div className="placeholder-icon">üìÅ</div>
                    <div className="placeholder-text">Ïù¥ÎØ∏ÏßÄ Ï§ÄÎπÑ Ï§ë</div>
                  </div>
                )}
              </div>
              <div className="project-content">
                <h3 className="project-title">{project.title}</h3>
                <p className="project-description">{project.description}</p>
                <a
                  href={project.link}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="project-link"
                >
                  View Project ‚Üí
                </a>
              </div>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
});

export default Projects;

